import React from 'react';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import '../styles/Proyectos/EcoRuta/EcoRutaPC.css';
import '../styles/Proyectos/EcoRuta/EcoRutaMOBILE.css';

function EcoRuta() {
  return (
    <div className="ecoruta-container">
      <h1>EcoRuta Project</h1>
      <p>Este es el proyecto EcoRuta. Aquí encontrarás información detallada sobre cómo implementamos la ruta.</p>
      
      {/* Imagen del celular */}
      <div className="phone-frame">
        {/* Slider de imágenes */}
        <Carousel autoPlay infiniteLoop showThumbs={false}>
          <div>
            <img src="img/UI1.jpg" alt="Imagen 1" />
          </div>
          <div>
            <img src="/img/UI2.jpg" alt="Imagen 2" />
          </div>
          <div>
            <img src="img/UI3.jpg" alt="Imagen 3" />
          </div>
        </Carousel>
      </div>

      <a href="/Portafolio-ThonyMarckDEV" className="btn">Volver al Inicio</a>
    </div>
  );
}

export default EcoRuta;
